"use client"

import { useParams } from "next/navigation"
import { getBookById } from "@/lib/books"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { AlertDialog, AlertDialogAction, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import Link from "next/link"
import Image from "next/image"

export default function BookDetailPage() {
  const params = useParams()
  const bookId = params.id as string
  const book = getBookById(bookId)

  if (!book) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <AlertDialog defaultOpen>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Book Not Found</AlertDialogTitle>
              <AlertDialogDescription>
                The book you are looking for could not be found. It may have been removed or the URL might be incorrect.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <Link href="/catalog">
                <AlertDialogAction>Return to Catalog</AlertDialogAction>
              </Link>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4">
        <Card className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Book Cover */}
            <div className="relative h-[400px] w-full">
              <Image
                src={book.coverUrl}
                alt={book.title}
                fill
                className="object-cover rounded-lg"
                priority
              />
            </div>

            {/* Book Details */}
            <div>
              <h1 className="text-3xl font-bold mb-2">{book.title}</h1>
              <p className="text-xl text-gray-600 mb-4">by {book.author}</p>
              
              <div className="space-y-4">
                <div>
                  <h2 className="font-semibold text-gray-700">Category</h2>
                  <p>{book.category}</p>
                </div>
                
                <div>
                  <h2 className="font-semibold text-gray-700">ISBN</h2>
                  <p>{book.isbn}</p>
                </div>
                
                <div>
                  <h2 className="font-semibold text-gray-700">Status</h2>
                  <span className={`inline-block px-3 py-1 rounded-full text-sm ${
                    book.available ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                  }`}>
                    {book.available ? 'Available' : 'Borrowed'}
                  </span>
                </div>
                
                <div>
                  <h2 className="font-semibold text-gray-700">Description</h2>
                  <p className="text-gray-600">{book.description}</p>
                </div>

                <div className="pt-4 space-x-4">
                  <Button
                    disabled={!book.available}
                    className="w-32"
                  >
                    {book.available ? 'Borrow' : 'Unavailable'}
                  </Button>
                  <Link href="/catalog">
                    <Button variant="outline">
                      Back to Catalog
                    </Button>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </Card>
      </div>
    </div>
  )
}
